<template>
  <v-container>
    <div v-if="currentCaseFile" class="edit-form py-3">
      <v-row>
        <v-col class="text-center" cols="12" md="4">
          <v-row class="pa-5">
            <h2>EDIT STAGE</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/addStage"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >ADD STAGE</v-btn
          >
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/stages"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >STAGE LIST</v-btn
          >
        </v-col>
      </v-row>
      <div>
        <v-form ref="form" lazy-validation>
          <v-select
            v-model="currentCaseFile.refType"
            :items="refType"
            item-text="description"
            item-value="name"
            :menu-props="{ maxHeight: '400' }"
            label="DEPARTMENT"
            hint="Set the casefile department"
            persistent-hint
          ></v-select>

          <v-select
            v-model="currentCaseFile.year"
            :items="year"
            label="Year"
          ></v-select>

          <v-select
            v-model="currentCaseFile.branch"
            :items="branch"
            item-text="name"
            item-value="brCode"
            :menu-props="{ maxHeight: '400' }"
            label="BRANCH"
            hint="Set the casefile branch"
            persistent-hint
          ></v-select>

          <v-select
            v-model="currentCaseFile.insurer"
            :items="insurer"
            item-text="name"
            item-value="insCode"
            :menu-props="{ maxHeight: '400' }"
            label="INSURER"
            hint="Set the casefile insurer"
            persistent-hint
          ></v-select>

          <v-text-field
            v-model="currentCaseFile.claimNo"
            :rules="[(v) => !!v || 'Insurers reference is required']"
            label="Insurers Reference"
            required
          ></v-text-field>

          <v-text-field
            v-model="currentCaseFile.vehicleNo"
            :rules="[(v) => !!v || 'Vehicle Number is required']"
            label="Vehicle Number Reference"
            required
          ></v-text-field>
          <!-- {{ formatDate(currentCaseFile.dateOfLoss) }}<br /> -->
          <!-- {{ dateFormat }} -->

          {{ computedDateFormatted }}
          <template>
            <div>
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="computedDateFormatted"
                    label="Date of Loss"
                    prepend-icon="mdi-calendar"
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="computedDateFormatted"
                  :active-picker.sync="activePicker"
                  :max="
                    new Date(
                      Date.now() - new Date().getTimezoneOffset() * 60000
                    )
                      .toISOString()
                      .substr(0, 10)
                  "
                  min="1950-01-01"
                  @change="save"
                ></v-date-picker>
              </v-menu>
            </div>
          </template>

          <v-text-field
            v-model="currentCaseFile.placeOfLoss"
            :rules="[(v) => !!v || 'Place of Loss is required']"
            label="Place of Loss"
            required
          ></v-text-field>

          <v-text-field
            v-model="currentCaseFile.insuredName"
            :rules="[(v) => !!v || 'Insured Name is required']"
            label="Insured Name"
            required
          ></v-text-field>
          <v-text-field
            v-model="currentCaseFile.insComment"
            label="Insurers Comment"
          ></v-text-field>
        </v-form>

        <v-btn
          :disabled="isAddButtonDisabled"
          color="primary"
          class="mt-3"
          @click="updateCaseFile"
          >Submit</v-btn
        >
      </div>
      <!-- <v-form ref="form" lazy-validation>
        <v-text-field
          v-model="currentCaseFile.name"
          :rules="[(v) => !!v || 'Name is required']"
          label="Name"
          required
        ></v-text-field>

        <v-text-field
          v-model="currentCaseFile.stageCode"
          :rules="[(v) => !!v || 'Code is required']"
          label="Code"
          required
        ></v-text-field>

        <v-btn color="error" small class="mr-2" @click="deleteStage">
          Delete
        </v-btn>

        <v-btn color="success" small @click="updateStage"> Update </v-btn>
      </v-form> -->

      <!-- <p class="mt-3">{{ message }}</p> -->
    </div>

    <!-- <div v-else>
      <p>Please click on a Stage...</p>
    </div> -->
  </v-container>
</template>

<script>
import BranchDataService from "../../services/BranchDataService";
import DeptDataService from "../../services/DeptDataService";
import InsDataService from "../../services/InsDataService";
import CasefileDataService from "../../services/CasefileDataService";
import moment from "moment";

export default {
  name: "stage",
  data() {
    return {
      activePicker: null,
      date: null,
      menu: false,

      refType: [],
      branch: [],
      insurer: [],
      year: ["2018", "2019", "2020", "2021", "2022"],
      currentCaseFile: {
        id: null,
        refType: "",
        year: "",
        branch: "",
        insurer: "",
        claimNo: "",
        vehicleNo: "",
        dateOfLoss: "",

        placeOfLoss: "",
        insuredName: "",
        insComment: "",
        createdBy: "",
        fileStatus: "NEW",
      },

      // currentCaseFile: null,
      // message: "",
    };
  },

  watch: {
    menu(val) {
      val && setTimeout(() => (this.activePicker = "YEAR"));
    },
  },

  computed: {
    isAddButtonDisabled() {
      return !(
        this.currentCaseFile.refType &&
        this.currentCaseFile.year &&
        this.currentCaseFile.branch &&
        this.currentCaseFile.insurer &&
        this.currentCaseFile.claimNo &&
        this.currentCaseFile.vehicleNo &&
        this.currentCaseFile.dateOfLoss &&
        this.currentCaseFile.placeOfLoss &&
        this.currentCaseFile.insuredName &&
        this.currentCaseFile.insComment &&
        // this.currentCaseFile.createdBy &&
        this.currentCaseFile.fileStatus
      );
    },

    // dateFormat() {
    //   return this.currentCaseFile.dateOfLoss
    //     ? moment(this.currentCaseFile.dateOfLoss).format("YYYY-MM-DD")
    //     : "";
    // },

    computedDateFormatted() {
      return this.formatDate(this.currentCaseFile.dateOfLoss);
    },
  },

  methods: {
    retrieveBranch() {
      BranchDataService.getAll()
        .then((response) => {
          this.branch = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveDept() {
      DeptDataService.getAll()
        .then((response) => {
          this.refType = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveIns() {
      InsDataService.getAll()
        .then((response) => {
          this.insurer = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getCaseFile(id) {
      CasefileDataService.get(id)
        .then((response) => {
          this.currentCaseFile = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    updateCaseFile() {
      CasefileDataService.update(
        this.currentCaseFile.id,
        this.currentCaseFile,
        this.currentCaseFile.createdBy == this.$store.state.auth.user.username
      )
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "casefiles" });
          // this.message = "The Stage was updated successfully!";
        })
        .catch((e) => {
          console.log(e);
        });
    },

    deleteStage() {
      CasefileDataService.delete(this.currentCaseFile.id)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "casefile-list" });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    formatDate(value) {
      return moment(value).format("YYYY-MM-DD");
    },

    // dateFormat() {
    //   return this.currentCaseFile.dateOfLoss
    //     ? moment(this.currentCaseFile.dateOfLoss).format("YYYY-MM-DD")
    //     : "";
    // },

    // formatDate(date) {
    //   if (!date) return null;

    //   const [year, month, day] = date.split("-");
    //   return `${month}/${day}/${year}`;
    // },

    save(date) {
      this.$refs.menu.save(date);
    },
  },
  mounted() {
    // this.message = "";
    this.getCaseFile(this.$route.params.id);
    this.retrieveBranch();
    this.retrieveDept();
    this.retrieveIns();
  },
};
</script>
